import React, { Component } from 'react'
import Link from 'gatsby-link'
import BourbonParty2 from '../assets/images/BB_Bourbon-70.jpg'
import BourbonParty3 from '../assets/images/BB_Bourbon-33.jpg'
import BourbonParty4 from '../assets/images/BB_Bourbon-78.jpg'

class PartyContent extends Component {
    render() {
        return (
            <section>
                <div className="container">
                    <div className="columns">
                        <div className="column"></div>
                        <div className="column is-half">
                            <div className="content m-t-xxl">
                                <p className="is-size-3 has-text-centered has-text-weight-bold">Tips for Hosting Your Party</p>
                            </div>
                        </div>
                        <div className="column"></div>
                    </div>
                    <div className="tile is-ancestor">
                        <div className="tile is-vertical is-parent">
                            <div className="tile is-child">
                                <a href="/" className="button is-fullwidth is-primary m-t-sm">
                                    Buy Bundle
                                </a>
                                <Link to="/tutorials" className="button is-fullwidth is-primary m-t-sm">
                                    View Tutorials
                                </Link>
                                <div className="card m-t-sm">
                                    <header className="card-header">
                                        <p className="card-header-title">
                                            Bourbon Party
                                        </p>
                                    </header>
                                    <div className="card-image">
                                        <figure className="image is-4by3">
                                            <img src={BourbonParty2} />
                                        </figure>
                                    </div>
                                    <div className="card-content">
                                        <div className="content">
                                            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Phasellus nec iaculis mauris.

                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="tile is-9 is-parent">
                            <div className="tile is-child">
                                <div className="tile is-child">
                                    <div className="card">
                                        <div className="card-image">
                                            <figure className="image is-4by3">
                                                <img src={BourbonParty4} />
                                            </figure>
                                        </div>
                                        <footer className="card-footer">
                                            <a href="#" className="card-footer-item">Watch Videos</a>
                                        </footer>
                                    </div>
                                    <div className="card m-t-md">
                                        <div className="card-image">
                                            <figure className="image is-4by3">
                                                <img src={BourbonParty3} />
                                            </figure>
                                        </div>
                                        <footer className="card-footer">
                                            <Link to="/tutorials" className="card-footer-item">
                                                Read Tutorials
                                            </Link>
                                        </footer>
                                    </div>
                                </div>
                            </div>
                            <div className="tile is-child">
                                <div className="content m-l-lg">
                                    <p className="title">What is in the bundle?</p>
                                    <ul>
                                        <li>Something</li>
                                    </ul>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}


export default PartyContent


